/* =================== !CLEARS */
.cf:before, .cf:after {
  content: "";
  display: table; }

.cf:after {
  clear: both; }

.clear {
  clear: both; }

.clear-left {
  clear: left; }

.clear-right {
  clear: right; }

/* =================== !ALIGNMENT */
.text-left {
  text-align: left; }

.text-right {
  text-align: right; }

.text-center {
  text-align: center; }

.justify {
  text-align: justify; }
  .justify:after {
    content: "";
    display: inline-block;
    height: 0;
    width: 100%; }

.alignleft {
  float: left; }
  .alignleftimg {
    margin: 0.5rem 5.7rem 2.85rem 0; }

.alignright {
  float: right; }
  .alignrightimg {
    margin: 0.5rem 0 2.85rem 5.7rem; }

.aligncenter {
  display: block;
  margin: 0 auto; }
  .aligncenterimg {
    margin: 0.5rem auto 5.7rem; }

/* =================== !GRID */
.w12 {
  width: 12.5%; }

.w25 {
  width: 25%; }

.w33 {
  width: 33.33%; }

.w50 {
  width: 50%; }

.w66 {
  width: 66.66%; }

.w75 {
  width: 75%; }

.w100 {
  width: 100%; }

.container {
  max-width: 136.8rem;
  margin-left: auto;
  margin-right: auto;
  padding-left: 2.85rem;
  padding-right: 2.85rem; }
  .container--no-pad, .container.g {
    padding-left: 0;
    padding-right: 0; }

.g {
  font-size: 0; }
  .g--pulled {
    margin-left: -2.85rem;
    margin-right: -2.85rem; }
  .g > * {
    font-size: medium; }
  .g__col {
    display: inline-block;
    vertical-align: top; }
    .g__col--pad {
      padding-left: 2.85rem;
      padding-right: 2.85rem; }

