/* ================================ !TYPOGRAPHY */
/* =================== !LINKS */
a {
  color: #000000;
  text-decoration: none;
  outline: 0;
  transition: 0.3s cubic-bezier(0.075, 0.82, 0.165, 1); }
  a:hover, a:focus {
    color: #1a1a1a; }

/* =================== !BASE-TYPE */
html {
  font-size: 62.5%; }

body {
  font-family: sans-serif;
  color: #000000;
  font-size: 1.4rem; }

/* =================== !HEADINGS */
h1, h2, h3, h4, h5, h6 {
  line-height: 1.2;
  font-family: serif;
  margin-bottom: 2.85rem;
  font-weight: normal; }

h1,
.h1 {
  font-size: 3.5rem; }

h2,
.h2 {
  font-size: 3rem; }

h3,
.h3 {
  font-size: 2.4rem; }

h4,
.h4 {
  font-size: 2rem; }

h5,
.h5 {
  font-size: 1.6rem; }

p {
  line-height: 1.6; }

/* =================== !ARTICLES */
article p {
  margin-bottom: 5.7rem; }

article blockquote {
  font-style: italic;
  padding: 5.7rem;
  margin-bottom: 5.7rem; }
  article blockquote > p {
    margin-bottom: 0; }

article ul,
article ol {
  padding-left: 5.7rem;
  margin: 5.7rem 0;
  list-style-position: outside;
  list-style-type: disc; }

article ol {
  list-style-type: decimal; }

article li {
  margin: 2.85rem 0; }

/* =================== !WORDPRESS */
.wp-caption {
  max-width: 100% !important;
  margin-bottom: 5.7rem; }
  .wp-caption > a {
    display: block; }

.wp-caption-text {
  text-align: center;
  font-size: 80%;
  margin: .5rem 0; }

